body{
  background-color: #f2f2f2;
}

.movies{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width:80%;
}

.movies__movie{
  width: 45%;
  background-color: #fff;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;;
  color: #adaeb9;
  box-shadow:0 13px 27px -5px rgba(50, 50, 93, 0.25),
  0 8px 16px -8px rgba(0,0,0, 0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
  font-size: 13px;
  word-break: break-all;
    white-space: break-spaces;
}

.movies__movie img{
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;;
  margin-right: 30px;
  box-shadow:0 13px 27px -5px rgba(50, 50, 93, 0.25),
  0 8px 16px -8px rgba(0,0,0, 0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movies__movie .movie__title, .movies__movie .movie__year{
  margin: 0;
  font-weight: 300;
}

.movies__movie .movie__title{
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movies__movie .movie__genres{
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0;
}

.movie__genres li{
  margin-right: 10px;
  font-size: 13px;
}